@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;1,200;1,300;1,400&display=swap);
body {
  margin: 0 auto;
  font-family: 'Nunito', sans-serif;
}

code {
  font-family: 'Nunito', sans-serif;
}

*{
  margin: 0;
}

.card{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 50px auto;
}

.img__box{
  border-radius: 24px;
  box-shadow: 0 0 5px rgb(177, 175, 175);
  width: 300px;
  height: 450px;
  margin: 0 auto;
}

.img__box{
  background-image: url(/coding_bootcamp-react_card/static/media/prod-img.835f0e14.png);
  background-size: cover;
  background-position: -22px -115px;
  background-repeat: no-repeat;
}

.card__text{
  border-radius: 24px;
  box-shadow: 0 0 5px rgb(177, 175, 175);
  width: 300px;
  height: 450px;
  margin: 0 auto;
}

.card__text{
    background: #fff;
    -webkit-transform: translateY(-175px);
            transform: translateY(-175px);
    box-shadow: 0 0 5px rgb(177, 175, 175);
  }

  .circle__item li{
    display: inline-block;
    margin-right: 10px;
    margin-top: 10px;
  }
  
  .circle{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #c4c4c4;
    text-align: center;
  }
.title-price__box{
    padding: 50px 40px 0;
    display: flex;
    justify-content: space-between;
    font-size: 14px;
  }
.paragraph{
    padding: 20px 40px;
    margin: 0;
  }
.colors__box h3{
  padding-left: 40px;
  font-size: 16px;
}

  .circle1{
    background-color: #110101;
  }
  .circle2{
    background-color: #2c1286;
  }
  .circle3{
    background-color: #dfcc40;
  }
  .circle4{
    background-color: #596662;
  }
  
.sizes__box h3{
    padding-left: 40px;
    font-size: 16px;
  }

.sizes__box{
    margin-top: 15px;
  }
  
  .circle--active{
    background-color: #000;
    color: #fff;
  }
.card__button{
    display: block;
    text-align: center;
    background-color: #000;
    border: 2px solid #000;
    padding: 10px 55px;
    margin: 40px auto 10px;
    cursor: pointer;
    font-weight: bold;
    border-radius: 8px;
    color: #fff;
  }
